import React from 'react'
import { useHistory } from 'react-router-dom'
import Header from '../components/Header'
import './Home.page.css'

export default function Home() {
  const history = useHistory()

  return (
    <>
      <Header
        title={
          <>
            <span className="highlight">My</span>World
          </>
        }
        subtitle={
          <>
            An NFT gallery of <span className="highlight">arts and music</span>{' '}
            for autism community
            <div>
              Slow and steady <span className="highlight">wins</span> the race
            </div>
            <div 
              className="btn btn-bordered btn-light" 
              style={{width: "15em", lineHeight: "2em", display: "block", margin: "20px auto"}}
              onClick={() => window.open('https://www.pinksale.finance/#/launchpad/0x6fAEB141145C0c1C32C51d169a73fE9b93f480d1?chain=BSC')}
            >
              üè∑Ô∏è Buy MyW 
            </div>
          </>
        }
      ></Header>

      <img
        className="header-image"
        alt="Header"
        onClick={() => history.push('/packs')}
        src={`${process.env.PUBLIC_URL}/assets/PinWheel.png`}
      />
    </>
  )
}
