import React from 'react'

import useMetaMask from '../providers/MetaMaskProvider'
import './AccountDetails.css'

export default function Wallet() {
  const {
    connect,
    disconnect,
    isActive,
    account,
  } = useMetaMask()

  return (
    <div className="wallet__popup">
        <div className="btn btn-small" onClick={() => window.open('https://t.me/mywnft', '_blank')}>
          üíÅ Telegram
        </div>
      {isActive ? (
        <>
          <div className="wallet__item">üëõ {account}</div>
          <div className="btn btn-small" onClick={() => disconnect()}>
            üëã Logout
          </div>
        </>
      ) : (
        <div className="btn btn-small" onClick={() => connect()}>
          ü§ù Login
        </div>
      )}
      {/* <div onClick={() => testScript()} className="btn btn-small">Test Script</div> */}
    </div>
  )
}
